@keyframes avatarBlinking {
  0% {
    transform: scale(1);
    box-shadow: 1px 2px 37px -13px rgba(212, 20, 67, .4);
  }

  100% {
    transform: scale(1.2);
    box-shadow: -1px 1px 38px 12px rgba(212, 20, 67, .1);
  }
}

@keyframes fade-bottom-to-top {
  from {
    opacity: .5;
    transform: translateY(50px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes reviewTextAnimation {
  0% {
    transform: scale(1) translateX(calc(-100% * (var(--current-review-number)  - 1) ) );
  }

  35% {
    transform: scale(.75) translateX(calc(-100% * (var(--current-review-number)  - 1) ) );
  }

  65% {
    transform: scale(.75) translateX(calc(-100% * (var(--next-review-number)  - 1) ) );
  }

  100% {
    transform: scale(1) translateX(calc(-100% * (var(--next-review-number)  - 1) ) );
  }
}

